<script>
  import lottie from "lottie-web";
  import { onMount } from "svelte";

  import { randHex } from "../../../lib/util.js";
  export let data;
  let lottieLogo;
  let isLottiePlayed = false;
  let lottieID = "lottieID-" + randHex(8);
  onMount(() => {
    lottieLogo = lottie.loadAnimation({
      container: document.getElementById(lottieID),
      renderer: "svg",
      loop: false,
      autoplay: false,
      path: data.lottiePath,
    });
  });

  function playLottie(event) {
    if (!isLottiePlayed) {
      lottieLogo.play();
    } else {
      lottieLogo.stop();
    }
    isLottiePlayed = !isLottiePlayed;
  }
</script>

<style>
  .lottie-card {
    padding: 10px;
  }
</style>

<div class="col s12 m2 l2">
  <div class="card grey darken-4 lottie-card">
    <span class="card-title double-height-title"> {data.title} </span>
    <div class="card-content gray-text">
      <div class="lottie-display" on:click={playLottie} id={lottieID} />
    </div>
  </div>
</div>
